body {
  margin: 0px; }

#animator1,
#animator2 {
  position: absolute;
  width: 50%;
  height: 100%; }
  #animator1 .animation,
  #animator2 .animation {
    width: 100%; }
  #animator1 .settings .field,
  #animator2 .settings .field {
    height: 40px;
    font-size: 24px; }
    #animator1 .settings .field input,
    #animator2 .settings .field input {
      float: right;
      font-size: 24px;
      width: 100px; }

#animator2 {
  border-left: 1px solid;
  left: 50%; }

#controller {
  position: absolute;
  display: inline-block;
  bottom: 0px;
  width: 100%;
  text-align: center; }
  #controller button {
    font-size: 24px;
    width: 25%;
    padding: 10px 0px; }



